import React from "react";
import logo from "../../assets/images/logo.svg";
export default function NavBar() {
  return (
    <header className="header">
      <div className="overlay has-fade"></div>
      <div className="container container--pt flex flex-jc-c hide-for-mobile">
        <a href="/" className="header__logo">
          <img src={logo} alt="Easybank" />
        </a>
      </div>
      <nav className="container flex flex-jc-sb flex-ai-c">
        <a href="/" className="header__logo hide-for-desktop">
          <img src={logo} alt="Easybank" />
        </a>
        <a
          id="btnHamburger"
          href="#"
          className="header__toggle hide-for-desktop"
        >
          <span></span>
          <span></span>
          <span></span>
        </a>

        <div className="header__links hide-for-mobile">
          <a href="#">FOOTPRINT</a>
          <a href="#">INNOVATIONS</a>
          <a href="#">ACADEMIC</a>
          <a href="#">JOBS</a>
          <a href="#">TENDERS</a>
          <a href="#">TEAM</a>
        </div>
        <div className="header__links hide-for-mobile">
          <a href="#">PROGRAM</a>
          <a href="#">CONTACT</a>
          <a href="#">HELP</a>
        </div>
      </nav>
      <div className="header__menu has-fade">
        <a href="#">FOOTPRINT</a>
        <a href="#">INNOVATIONS</a>
        <a href="#">ACADEMIC</a>
        <a href="#">JOBS</a>
        <a href="#">TENDERS</a>
        <a href="#">TEAM</a>
        <a href="#">PROGRAM</a>
        <a href="#">CONTACT</a>
        <a href="#">HELP</a>
      </div>
    </header>
  );
}
